import React, { Component } from 'react';
import './Todo.css';

class Todo extends Component {
  render() {
    return (
    		<div id="todo" className="todo">
    			<input type="checkbox" name="complete" value="Done" id="complete" onclick="findIdAndUpdate(this)"></input>
    			<button type="button" name="delete" id="delete" onclick="findIdAndDelete(this)">Delete</button>
    			<div id="todoText">New Todo</div>
    		</div>
    );
  }
}

export default Todo;
